.cashback-info .cat-item {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center; }
  .cashback-info .cat-item:nth-of-type(1) {
    transform: translateX(-10px); }
  .cashback-info .cat-item:nth-of-type(3) {
    transform: translateX(10px); }
  .cashback-info .cat-item img {
    transition: transform 0.3s ease; }
    .cashback-info .cat-item img:hover {
      transform: scale(1.1);
      filter: hue-rotate(290deg) brightness(1.3); }
    .cashback-info .cat-item img:active {
      transform: scale(1); }
  .cashback-info .cat-item h5 {
    font-weight: bold;
    font-size: 12px;
    color: #2C3652; }

.cashback-info .form-landing-information-container img {
  width: 100%;
  height: auto; }

.cashback-info .info-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center; }
  .cashback-info .info-container .how-info-container {
    padding-left: 67px;
    margin: 25px 0; }
    @media screen and (max-width: 767px) {
      .cashback-info .info-container .how-info-container {
        padding-left: 10px; } }
    .cashback-info .info-container .how-info-container h5 {
      font-weight: bold;
      font-size: 18px;
      color: #2C3652; }
    .cashback-info .info-container .how-info-container p {
      font-weight: 600;
      font-size: 14px;
      color: #6E7F95; }
